import React, { FormEvent, useState } from "react";

export type Track = {
  id: string;
  name: string;
  artists: string;
  album?: string;
  preview_url?: string | null;
  spotify_url?: string;
  image_url?: string | null;
  duration_ms?: number;
};

type SongSearchProps = {
  onSelect: (track: Track) => void;
};

const SongSearch: React.FC<SongSearchProps> = ({ onSelect }) => {
  const [query, setQuery] = useState("");
  const [results, setResults] = useState<Track[]>([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    const trimmedQuery = query.trim();

    if (!trimmedQuery) {
      setError("Bitte gib einen Suchbegriff ein.");
      setResults([]);
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const response = await fetch(
        `/api/search?q=${encodeURIComponent(trimmedQuery)}`
      );
      const data: { tracks?: Track[]; error?: string } = await response
        .json()
        .catch(() => ({}));

      if (!response.ok) {
        throw new Error(data?.error ?? "Die Suche war nicht erfolgreich.");
      }

      setResults(data.tracks ?? []);
    } catch (err) {
      console.error("Song search failed", err);
      setError(
        "Beim Suchen ist ein Fehler aufgetreten. Bitte versuch es erneut."
      );
      setResults([]);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col gap-4">
      <form onSubmit={handleSubmit} className="flex flex-col gap-2 sm:flex-row">
        <input
          type="text"
          value={query}
          onChange={(event) => setQuery(event.target.value)}
          placeholder="Song oder Artist eingeben..."
          className="w-full rounded border border-gray-200 px-3 py-2 text-sm focus:border-gray-400 focus:outline-none"
        />
        <button
          type="submit"
          disabled={loading}
          className="rounded bg-black px-4 py-2 text-sm font-medium text-white transition hover:bg-gray-800 disabled:cursor-not-allowed disabled:opacity-70"
        >
          {loading ? "Suche..." : "Suchen"}
        </button>
      </form>

      {error && <p className="text-sm text-red-600">{error}</p>}

      {!loading && !error && results.length === 0 && (
        <p className="text-sm text-gray-500">
          Gib einen Suchbegriff ein, um Songs zu finden.
        </p>
      )}

      <div className="flex flex-col gap-3">
        {results.map((track) => (
          <button
            key={track.id}
            type="button"
            onClick={() => onSelect(track)}
            className="flex items-center gap-3 rounded border border-gray-100 bg-white p-3 text-left shadow-sm transition hover:bg-gray-50"
          >
            {track.image_url ? (
              // eslint-disable-next-line @next/next/no-img-element
              <img
                src={track.image_url}
                alt={track.name}
                className="h-14 w-14 rounded object-cover"
                loading="lazy"
              />
            ) : (
              <div className="flex h-14 w-14 items-center justify-center rounded bg-gray-200 text-xs text-gray-600">
                Kein Bild
              </div>
            )}
            <div className="flex flex-col">
              <span className="text-sm font-semibold text-gray-900">
                {track.name}
              </span>
              <span className="text-xs text-gray-600">{track.artists}</span>
              {track.album && (
                <span className="text-xs text-gray-400">{track.album}</span>
              )}
            </div>
          </button>
        ))}
      </div>
    </div>
  );
};

export default SongSearch;
